<template>
    <div class="easy-entry">
        <div class="public-detail">
            <div class="important-info">
                <Row>
                    <Col span="4">
                        <div class="name">基础货运要求</div>
                    </Col>
                    <Col span="20">
                        <Button type="primary" @click="save('formValidate')">保存</Button>
                    </Col>
                </Row>
            </div>
        </div>
        <div class="public-table-content public-input">
            <div class="l-content">
                <div class="table-data">
                    <ul class="transit">
                        <p class="table-title">基本信息</p>
                        <Form ref="formValidate" :model="transitModal" label-position="left" :label-width="100" :rules="ruleValidate">
                            <FormItem label="运输类型：" prop="name">
                                <Select
                                    v-model="transitModal.name"
                                    filterable
                                    remote
                                    :remote-method="transitRemote"
                                    :loading="transitLoading"
                                    @on-select="onChangeClick">
                                    <Option v-for="(option, index) in transitData" :value="option.value" :key="index">{{option.label}}</Option>
                                </Select>
                                <p class="select-value">
                                    <span v-for="(value, index) in transitSelect" :key="index">{{value}}<Icon type="ios-close-circle" @click="delTransit(index)"/></span>
                                </p>
                            </FormItem>
                            <FormItem label="运输类型：" prop="name">
                                <Select
                                    v-model="transitModal.name"
                                    filterable
                                    remote
                                    :remote-method="transitRemote"
                                    :loading="transitLoading"
                                    @on-select="onChangeClick">
                                    <Option v-for="(option, index) in transitData" :value="option.value" :key="index">{{option.label}}</Option>
                                </Select>
                                <p class="select-value">
                                    <span v-for="(value, index) in transitSelect" :key="index">{{value}}<Icon type="ios-close-circle" @click="delTransit(index)"/></span>
                                </p>
                            </FormItem>
                            <FormItem label="中转地：">
                                <Select
                                    v-model="transitModal.transit"
                                    filterable
                                    remote
                                    :remote-method="transitRemote"
                                    :loading="transitLoading"
                                    @on-select="onChangeClick">
                                    <Option v-for="(option, index) in transitData" :value="option.value" :key="index">{{option.label}}</Option>
                                </Select>
                                <p class="select-value">
                                    <span v-for="(value, index) in transitSelect" :key="index">{{value}}<Icon type="ios-close-circle" @click="delTransit(index)"/></span>
                                </p>
                            </FormItem>
                            <FormItem label="货物类型：" prop="type">
                                <Select v-model="transitModal.type" style="width:200px">
                                    <Option v-for="item in typeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                                </Select>
                            </FormItem>
                        </Form>
                    </ul>
                    <ul class="transit">
                        <p class="table-title">货运要求备注</p>
                        <Input v-model="transitModal.textarea" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="请输入货运要求备注，500字以内！"></Input>
                    </ul>
                    <div class="bottom">
                        <p>
                            <Icon type="ios-information-circle" />
                            如果需要准确匹配物流方案，请您<a href="">填写</a>详细信息
                            <Icon type="ios-arrow-forward" />
                        </p>
                    </div>
                </div>
            </div>
        </div>
        <adt-reconfirm-modal :showModal="isTransitModal" @on-visible-change="visibleChange" :message="transitText"></adt-reconfirm-modal>
    </div>
</template>

<script>
export default {
    name: 'ConfirmFreightRequest',
    data() {
        return {
            transitModal: {
                name: '',
                type: '',
                transit: '',
                age: '',
                textarea: ''
            },
            transitText: '',
            isTransitModal: false,
            ruleValidate: {
                name: [
                    { required: true, message: 'The name cannot be empty', trigger: 'change' }
                ],
                type: [
                    { required: true, message: 'The name cannot be empty', trigger: 'change' }
                ]
            },
            typeList: [
                {value: '箱', label: '箱'},
                {value: '盒子', label: '盒子'}
            ],
            transitLoading: false,
            transitData: [],
            list: ['加拿大', '北级', '北京', '加州','哈尔滨', '邯郸', '韩国'],
            transitSelect: [],
            checkList: []
        }
    },
    mounted() {
        this.transitText = `中转地过多会导致物流方案的费用过高，<br>时效过长，如需添加请<a class="modal-a" href="">联系客服</a>`
    },
    methods: {
        visibleChange(changeStatus) {
            this.isTransitModal = changeStatus
        },
        save(name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$Message.success('Success!');
                } else {
                    this.$Message.error('Fail!');
                }
            })
        },
        onChangeClick(val) {
            if(this.transitSelect.length >= 5) {
                this.isTransitModal = true
                return
            }
            this.transitSelect.push(val.value)
        },
        transitRemote (val) {
            if (val !== '') {
                this.transitLoading = true
                setTimeout(() => {
                    this.transitLoading = false
                    const list = this.list.map(item => {
                        return {
                            value: item,
                            label: item
                        }
                    })
                    this.transitData = list.filter(item => item.label.toLowerCase().indexOf(val.toLowerCase()) > -1)
                }, 200)
            } else {
                this.transitData = []
            }
        },
        delTransit (index) {
            this.transitSelect.splice(index, 1)
        }
    }
}
</script>
<style lang="scss" scoped>
.public-detail{
    .ivu-col-span-20{
        display: block;
        text-align: right;
    }
}
.ivu-form-item{
    margin-bottom: 0;
}
.public-input{
    .l-content{
        width: 100%;
    }
    .ivu-form-item{
        /deep/.ivu-form-item-label{
            width: 170px!important;
            text-align: right;
            background: #F5F6F8;
            padding-right: 30px;
            border: 1px solid #e6e6e6;
            border-bottom: none;
            vertical-align: middle;
        }
        /deep/.ivu-form-item-content{
            padding-left: 30px;
            padding-right: 30px;
            width: 84%;
            border: 1px solid #e6e6e6;
            border-bottom: none;
            border-left: none;
            background: #fff;
            overflow-x: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            vertical-align: middle;
            .ivu-select{
                width: 200px;
                background:rgba(249,249,249,1);
                border-radius:4px;
                .ivu-select-selection{
                    background:rgba(249,249,249,1);
                }
            }
        }
        &:last-child{
            /deep/.ivu-form-item-label{
                border-bottom: 1px solid #e6e6e6;
            }
            /deep/.ivu-form-item-content{
                border-bottom: 1px solid #e6e6e6;
            }
        }
    }
    .transit{
        margin-right: 0!important;
        .ivu-select{
            width: 20%;
        }
        .select-value{
            display: inline-block;
            font-weight: 400;
            font-size: 12px;
            vertical-align: middle;
            margin-left: 20px;
            span{
                background-color: #f4f4f4;
                padding: 6px 4px;
                border-radius: 4px;
                margin-left: 10px;
            }
            .ivu-icon-ios-close-circle{
                margin-left: 10px;
                color: #bbb6b6;
                font-size: 14px;
                cursor: pointer;
                &:hover{
                    color: $light-color;
                }
            }
        }
        /deep/.ivu-form-item-error-tip{
            top: 14%;
            left: 26%;
        }
    }
    .bottom{
        background: #f9f9f9;
        height: 300px;
        p{
            text-align: center;
            padding-top: 10px;
            .ivu-icon-ios-information-circle{
                color: $warning-color;
            }
            a{
                text-decoration: underline;
            }
        }
    }
}
</style>